import React from "react";
import data from '../smells.json';
import Accordion from 'react-bootstrap/Accordion';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../App.css';
import { Link } from "react-router-dom"

const Smells = () => {
    return (
        data && data.map(smell => {
            return (
                <div className="box" key={smell.id}>
                    <Accordion defaultActiveKey="0">
                        <Accordion.Item eventKey="0">
                            <Accordion.Header>{smell.name}</Accordion.Header>
                            <Accordion.Body>
                                <strong>Security properties affected:</strong> {smell['properties affected']} <br /> <br />
                                <strong>Description:</strong> {smell.description} <br /> <br />
                                <strong>Refactoring Type:</strong> {smell['refactoring Type']} <br /> <br />
                                <strong>Refactoring description</strong> {smell.refactoring} <br /><br />
                                <Link 
                                    className="btn btn-primary"
                                    to={{pathname: `/smell/${smell.name}`}}
                                    state={smell}
                                >
                                    Go to the SIG-based analysis
                                </Link>
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>
                </div>
            )
        })
    );
};

export default Smells;