import React from "react";
import { Link, useLocation } from 'react-router-dom';
//import data from '../smells.json';
import cloud from '../images/cloud.png';
import cloudBold from '../images/cloud_bold.png';
import './Sig.css';
import Xarrow from "react-xarrows";

const Sig = () => {
  const location = useLocation();
  const smell = location.state;
  //const { id } = useParams();
  var Rrelated;
  return (
    <div id="sig-main">
      <center><h1>Sig of {smell.name}</h1><br /> <br /></center>
      <div id="security">
        <div id="security-main">
          <img src={cloud} alt="cloud" className="cloud" id="security-main-cloud" />
          <div id="sec-text"><p className="property">Security</p></div>
        </div>
        <div id="security-props">
          <div className="security-props-clouds">
            <img src={cloud} alt="cloud" className="cloud" id="cloud-confidentiality" title="Degree to which a product or system ensures that data are accessible only to those authorized to have access." />
            <p className="property" id="property-confidentiality" width="120">Confidentiality</p>
          </div>

          <div className="security-props-clouds">
            <img src={cloud} alt="cloud" className="cloud" id="cloud-integrity" title="Degree to which a system, product or component prevents unauthorized access to, or modification of, computer programs or data." />
            <p className="property" id="property-integrity" width="120">Integrity</p>
          </div>

          <div className="security-props-clouds">
            <img src={cloud} alt="cloud" className="cloud" id="cloud-authenticity" title="Degree to which the identity of a subject or resource can be proved to be the one claimed." />
            <p className="property" id="property-authenticity">Authenticity</p>
          </div>

        </div>
      </div>

      <Xarrow
        start="cloud-confidentiality"
        end="sec-text"
        color="black"
        strokeWidth={2}
        startAnchor="top"
        endAnchor="bottom"
        headSize={7}
      />
      <Xarrow
        start="cloud-integrity"
        end="sec-text"
        path="straight"
        color="black"
        strokeWidth={2}
        startAnchor="top"
        endAnchor="bottom"
        headSize={7}
      />
      <Xarrow
        start="cloud-authenticity"
        end="sec-text"
        color="black"
        strokeWidth={2}
        startAnchor="top"
        endAnchor="bottom"
        headSize={7}
      />

      <div id="sig-center">

        <div id="ms-dsg-principles">
          <div id="dsg-principles">
            <div id="dsg-prin-cloud-and-text">
              <img src={cloud} alt="cloud" className="cloud" id="dsg-prin-cloud" />
              <div id="principles-text"><p className="property">Design principles [Microservices]</p></div>
            </div>
          </div>

          <div id="dsg-prin-clouds">
            <div className="dsg-prin">
              <img src={cloud} alt="cloud" className="cloud" id="cloud-independent-deploy" />
              <p className="property" id="property-independent-deploy">Independent <br />deployability</p>
            </div>
            <div className="dsg-prin">
              <img src={cloud} alt="cloud" className="cloud" id="cloud-horizontal-scalability" />
              <p className="property" id="property-horizontal-scalability" width="120">Horizontal <br />scalability</p>
            </div>
            <div className="dsg-prin">
              <img src={cloud} alt="cloud" className="cloud" id="cloud-isolation-failures" />
              <p className="property" id="property-isolation-failures" width="120">Isolation of <br />failures</p>
            </div>
            <div className="dsg-prin">
              <img src={cloud} alt="cloud" className="cloud" id="cloud-decentralization" />
              <p className="property" id="property-decentralization" width="120">Decentralization</p>
            </div>

          </div>
        </div>

        <Xarrow
          start="property-independent-deploy"
          end="dsg-prin-cloud"
          color="black"
          strokeWidth={2}
          startAnchor="left"
          endAnchor="right"
          headSize={7}
        />

        <Xarrow
          start="property-horizontal-scalability"
          end="dsg-prin-cloud"
          color="black"
          strokeWidth={2}
          startAnchor="left"
          endAnchor="right"
          headSize={7}
        />

        <Xarrow
          start="property-isolation-failures"
          end="dsg-prin-cloud"
          color="black"
          strokeWidth={2}
          startAnchor="left"
          endAnchor="right"
          headSize={7}
        />

        <Xarrow
          start="property-decentralization"
          end="dsg-prin-cloud"
          color="black"
          strokeWidth={2}
          startAnchor="left"
          endAnchor="right"
          headSize={7}
        />

        <div id="dsg-decisions">

          <div id="decisions-smell">
            <div id="dsg-smell">
              <img src={cloudBold} alt="cloud" className="cloud" id="cloud-smell" />
              <p className="property" id="dsg-smell">{smell.name}</p>
            </div>
          </div>

          <div id="decisions-refactoring">
            <div id="dsg-refactoring">
              {smell["refactoring-related"].map((related) => {
                if (related.length > 0) {
                  Rrelated = "This refactoring is also used to mitigate the effects of the " + related + " smell";
                }
                return (null)
              })}
              <img src={cloudBold} alt="cloud" className="cloud" id="cloud-refactoring" title={Rrelated} />
              <p className="property" id="dsg-refactoring">{smell["refactoring-name"]}</p>
            </div>
          </div>

          {/* SMELL NEGATIVE IMPACTS */}
          {smell["smell-negatives"].map((negative) => {
            var start, end, where;
            if (negative.includes("confidentiality") || negative.includes("authenticity") || negative.includes("integrity")) {
              start = "top";
              end = { position: "bottom", offset: { x: -10 } };
              where = "cloud-smell";
            } else if (negative.includes("isolation") || negative.includes("scalability") || negative.includes("deploy") || negative.includes("decentralization")) {
              start = "left";
              end = { position: "right", offset: { y: 10 } };
              where = "cloud-smell";
            } else if (negative.includes("analysability") || negative.includes("modularity") || negative.includes("modifiability") || negative.includes("testability") || negative.includes("reusability")) {
              start = "bottom";
              end = { position: "top", offset: { x: -10 } };
              where = "dsg-smell"
            } else {
              start = "right";
              end = { position: "left", offset: { y: -10 } };
              where = "cloud-smell";
            }
            return (<Xarrow key={negative}
              start={where}
              end={negative}
              color="red"
              animateDrawing={1}
              labels={{ middle: <div style={{ fontSize: "1.3em", fontFamily: "fantasy", fontStyle: "italic", color: "red" }}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;-</div> }}
              strokeWidth={2}
              curveness={0.1}
              startAnchor={start}
              endAnchor={end}
              headSize={7}
              zIndex={-1}
            />)
          })}
          {/* SMELL POSITIVE IMPACTS */}
          {smell["smell-positives"].map((positive) => {
            var start, end, where;
            if (positive.includes("confidentiality") || positive.includes("authenticity") || positive.includes("integrity")) {
              start = "top";
              end = { position: "bottom", offset: { x: -10 } };
              where = "cloud-smell";
            } else if (positive.includes("isolation") || positive.includes("scalability") || positive.includes("deploy") || positive.includes("decentralization")) {
              start = "left";
              end = { position: "right", offset: { y: 10 } };
              where = "cloud-smell";
            } else if (positive.includes("analysability") || positive.includes("modularity") || positive.includes("modifiability") || positive.includes("testability") || positive.includes("reusability")) {
              start = "bottom";
              end = { position: "top", offset: { x: -10 } };
              where = "dsg-smell"
            } else {
              start = "right";
              end = { position: "left", offset: { y: -10 } };
              where = "cloud-smell";
            }
            return (<Xarrow key={positive}
              start={where}
              end={positive}
              color="green"
              labels={{ middle: <div style={{ fontSize: "1em", fontFamily: "fantasy", fontStyle: "italic", color: "green" }}>+</div> }}
              strokeWidth={2}
              animateDrawing={1}
              curveness={0.1}
              startAnchor={start}
              endAnchor={end}
              headSize={7}
              zIndex={-1}
            />)
          })}
          {/* REFACTORING NEGATIVE IMPACTS */}
          {smell["refactoring-negatives"].map((negative) => {
            var start, end, where;
            if (negative.includes("confidentiality") || negative.includes("authenticity") || negative.includes("integrity")) {
              start = "top";
              end = { position: "bottom", offset: { x: 10 } };
              where = "cloud-refactoring";
            } else if (negative.includes("isolation") || negative.includes("scalability") || negative.includes("deploy") || negative.includes("decentralization")) {
              start = "left";
              end = { position: "right", offset: { y: -10 } };
              where = "cloud-refactoring";
            } else if (negative.includes("analysability") || negative.includes("modularity") || negative.includes("modifiability") || negative.includes("testability") || negative.includes("reusability")) {
              start = "bottom";
              end = { position: "top", offset: { x: 10 } };
              where = "dsg-refactoring"
            } else {
              start = "right";
              end = { position: "left", offset: { y: 10 } };
              where = "cloud-refactoring";
            }
            return (<Xarrow key={negative}
              start={where}
              end={negative}
              color="red"
              labels={{ middle: <div style={{ fontSize: "1.3em", fontFamily: "fantasy", fontStyle: "italic", color: "red" }}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;-</div> }}
              strokeWidth={2}
              animateDrawing={1}
              curveness={0.1}
              startAnchor={start}
              endAnchor={end}
              headSize={7}
              zIndex={-1}
            />)
          })}

          {/* REFACTORING POSITIVE IMPACTS */}
          {smell["refactoring-positives"].map((positive) => {
            var start, end, where;
            if (positive.includes("confidentiality") || positive.includes("authenticity") || positive.includes("integrity")) {
              start = "top";
              end = { position: "bottom", offset: { x: 10 } };
              where = "cloud-refactoring";
            } else if (positive.includes("isolation") || positive.includes("scalability") || positive.includes("deploy") || positive.includes("decentralization")) {
              start = "left";
              end = { position: "right", offset: { y: -10 } };
              where = "cloud-refactoring";
            } else if (positive.includes("analysability") || positive.includes("modularity") || positive.includes("modifiability") || positive.includes("testability") || positive.includes("reusability")) {
              start = "bottom";
              end = { position: "top", offset: { x: 10 } };
              where = "dsg-refactoring"
            } else {
              start = "right";
              end = { position: "left", offset: { y: 10 } };
              where = "cloud-refactoring";
            }
            return (<Xarrow key={positive}
              start={where}
              end={positive}
              color="green"
              labels={{ middle: <div style={{ fontSize: "1em", fontFamily: "fantasy", fontStyle: "italic", color: "green" }}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;+</div> }}
              strokeWidth={2}
              animateDrawing={1}
              curveness={0.1}
              startAnchor={start}
              endAnchor={end}
              headSize={7}
              zIndex={-1}
            />)
          })}

        </div>

        <div id="performance">

          <div id="performance-efficiency">
            <div id="pf-main-cloud">
              <img src={cloud} alt="cloud" className="cloud" id="performance-cloud" />
              <p className="property">Performance efficiency</p>
            </div>
          </div>

          <div id="performance-props">
            <div className="performance-props-clouds">
              <img src={cloud} alt="cloud" className="cloud" id="cloud-time-behaviour" title="Degree to which the response and processing times and throughput rates of a product or system, when performing its functions, meet requirements." />
              <p className="property" id="property-time-behaviour">Time <br /> behaviour</p>
            </div>

            <div className="performance-props-clouds">
              <img src={cloud} alt="cloud" className="cloud" id="cloud-resource-utilization" title="Degree to which the amounts and types of resources used by a product or system, when performing its functions, meet requirements." />
              <p className="property" id="property-resource-utilization">Resource <br /> utilization</p>
            </div>

            <div className="performance-props-clouds">
              <img src={cloud} alt="cloud" className="cloud" id="cloud-capacity" title="Degree to which the maximum limits of a product or system parameter meet requirements." />
              <p className="property" id="property-capacity">Capacity</p>
            </div>
          </div>

          <Xarrow
            start="property-time-behaviour"
            end="performance-cloud"
            color="black"
            strokeWidth={2}
            startAnchor="right"
            endAnchor="left"
            headSize={7}
          />

          <Xarrow
            start="property-resource-utilization"
            end="performance-cloud"
            color="black"
            strokeWidth={2}
            startAnchor="right"
            endAnchor="left"
            headSize={7}
          />

          <Xarrow
            start="property-capacity"
            end="performance-cloud"
            color="black"
            strokeWidth={2}
            startAnchor="right"
            endAnchor="left"
            headSize={7}
          />

        </div>

      </div>

      <div id="maintainability">

        <div id="maintainleft">

        </div>

        <div id="maintaincenter">

          <div id="maintainability-props">

            <div className="maintainability-props-clouds">
              <img src={cloud} alt="cloud" className="cloud" id="cloud-modularity" title="Degree to which a system or computer program is composed of discrete components such that a change to one component has minimal impact on other components." />
              <p className="property" id="property-modularity">Modularity</p>
            </div>

            <div className="maintainability-props-clouds">
              <img src={cloud} alt="cloud" className="cloud" id="cloud-reusability" title="Degree to which an asset can be used in more than one system, or in building other assets." />
              <p className="property" id="property-reusability">Reusability</p>
            </div>

            <div className="maintainability-props-clouds">
              <img src={cloud} alt="cloud" className="cloud" id="cloud-analysability" title="Degree of effectiveness and efficiency with which it is possible to assess the impact on a product or system of an intended change to one or more of its parts, or to diagnose a product for deficiencies or causes of failures, or to identify parts to be modified." />
              <p className="property" id="property-analysability">Analysability</p>
            </div>

            <div className="maintainability-props-clouds">
              <img src={cloud} alt="cloud" className="cloud" id="cloud-modifiability" title="Degree to which a product or system can be effectively and efficiently modified without introducing defects or degrading existing product quality." />
              <p className="property" id="property-modifiability">Modifiability</p>
            </div>

            <div className="maintainability-props-clouds">
              <img src={cloud} alt="cloud" className="cloud" id="cloud-testability" title="Degree of effectiveness and efficiency with which test criteria can be established for a system, product or component and tests can be performed to determine whether those criteria have been met." />
              <p className="property" id="property-testability">Testability</p>
            </div>

          </div>

          <div id="maintainability-main">

            <div id="maintainability-main-prop">
              <img src={cloud} alt="cloud" className="cloud" id="maintainability-prop" />
              <p className="property" id="text-maintainability">Maintainability</p>
            </div>

          </div>

        </div>

        <Xarrow
          start="property-modularity"
          end="maintainability-prop"
          color="black"
          strokeWidth={2}
          startAnchor="bottom"
          endAnchor="top"
          headSize={7}
        />

        <Xarrow
          start="property-reusability"
          end="maintainability-prop"
          color="black"
          strokeWidth={2}
          startAnchor="bottom"
          endAnchor="top"
          headSize={7}
        />

        <Xarrow
          start="property-analysability"
          end="maintainability-prop"
          color="black"
          strokeWidth={2}
          startAnchor="bottom"
          endAnchor="top"
          headSize={7}
        />

        <Xarrow
          start="property-modifiability"
          end="maintainability-prop"
          color="black"
          strokeWidth={2}
          startAnchor="bottom"
          endAnchor="top"
          headSize={7}
        />

        <Xarrow
          start="property-testability"
          end="maintainability-prop"
          color="black"
          strokeWidth={2}
          startAnchor="bottom"
          endAnchor="top"
          headSize={7}
        />

        <div id="maintainright">

        </div>


      </div>



      <center><Link to="/">Go back to security smells</Link></center>
    </div>

  );
};

export default Sig;