import { useRouteError, Link } from "react-router-dom"

const NotFound = () => {
    const error = useRouteError();
    return (
        <div>
            <h1>404</h1>
            <h2> {error.statusText || error.message} D:</h2><br /> <br />
            <Link to="/">Go back to security smells</Link>
        </div>
    );
};

export default NotFound;